import Training from "./Training";
import { v4 as uuidv4 } from "uuid";

export default function TrainingList({ trainings, handlers }) {

  const {openAddWindow} = handlers

  return (
    <div className="training-list">
      <div className="list-header">
        <h2>Training List</h2>
        <button onClick={() => openAddWindow('training')}>+</button>
      </div>

      <ul>
        {trainings.map((training) => (
          <Training key={uuidv4()} training={training} handlers={handlers} />
        ))}
      </ul>
    </div>
  );
}
