export default function Topic({ topic, handlers }) {
  const { select, deleteItem, completeItem } = handlers;

  const options = () => {
    const optButClasses = ["topic-list-option", !topic.isSelected && "d-none"];
    return (
      <>
        <button
          onClick={(e) => {
            completeItem(e, topic.id);
          }}
          className={optButClasses.join(" ")}
        >
          {topic.isCompleted ? "❌" : "✔"}
        </button>
        <button
          onClick={(e) => deleteItem(e, topic.id)}
          className={optButClasses.join(" ")}
        >
          🚮
        </button>
      </>
    );
  };
  return (
    <li
      className={`topic-list-item ${topic.isSelected && "option"}`}
      onClick={(e) => select(e, topic.id)}
    >
      <span className="list-name">{topic.name}</span>
      <span
        className={`${topic.isCompleted && "green"} ${
          topic.isSelected && "d-none"
        }`}
      >
        ✔
      </span>
      {options()}
    </li>
  );
}
