import { useEffect, useReducer, useState } from "react";
import "./index.css";
import TrainingList from "./TrainingList";
import TopicList from "./TopicList";
import Add from "./Add";
import { trainingList } from "./data";
import { v4 as uuidv4 } from "uuid";

export default function TrainingKeeper() {
  const [tKeeper, setTKeeper] = useReducer(
    (
      tKeeper,
      { newTKeeper, newTraining, newTopic, delTrainingId, delTopicId }
    ) => {
      let tK = !newTKeeper ? { ...tKeeper } : { ...newTKeeper };

      newTraining && tK.trainings.push(newTraining);

      if (newTopic) {
        tK.trainings.forEach((training, index) => {
          if (training.isSelected === true) {
            tK.trainings[index].topics.push(newTopic);
          }
        });
      }

      const trainingSelected = tK.trainings.find(
        (training) => training.isSelected
      );

      // delete training
      if (delTrainingId) {
        tK.trainings.forEach((training, index) => {
          if (training.id === delTrainingId) {
            tK.trainings.splice(index, 1);
          }
        });
      }

      // delete topic
      if (delTopicId) {
        trainingSelected.topics.forEach((topic, index) => {
          if (topic.id === delTopicId) {
            trainingSelected.topics.splice(index, 1);
          }
        });
      }

      // filter duplicates
      tK = {
        trainings: tK.trainings,
        topics: (trainingSelected && trainingSelected.topics) || tK.topics,
      };
      let trainingIds = [];
      tK.trainings = tK.trainings.filter((training) => {
        if (trainingIds.includes(training.id)) {
          return false;
        }
        trainingIds.push(training.id);
        return true;
      });

      tK.trainings.forEach((training, index) => {
        let topicIds = [];
        tK.trainings[index].topics = tK.trainings[index].topics.filter(
          (topic) => {
            if (topicIds.includes(topic.id)) {
              return false;
            }
            topicIds.push(topic.id);

            return true;
          }
        );
      });
      return tK;
    },
    {
      trainings: [],
      topics: [],
    }
  );

  const [addWindow, setAddWindow] = useReducer((addWindow, addWhat = "") => {
    return {
      isHidden: addWhat === "" ? true : false,
      addWhat,
    };
  }, {});

  useEffect(() => {
    const getData = async () => {
      const result = await fetch("/data");
      const json = await result.json();
      setTKeeper({ newTKeeper: json });
    };
    getData();
  }, []);

  // useEffect(() => {

  //   const postData = async () => {
  //     const result = await fetch("/data", {
  //       method: "POST",
  //       headers: {
  //         "Content-type": "application/json",
  //       },
  //       body: JSON.stringify(tKeeper),
  //     });
  //     const json = await result.json();
  //     console.log(json);
  //   };
  //   postData();
  // }, [tKeeper]);

  const postData = async () => {
    const tK = { ...tKeeper };
    tK.trainings.forEach((training) => {
      delete training.isSelected;
    });

    const result = await fetch("/data", {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(tK),
    });
    const json = await result.json();
    console.log(json);
  };

  const add = (e, addWhat) => {
    e.preventDefault();
    const getFormData = (e) => {
      const children = e.target.children;
      let inputValues = {};
      for (let i = 0; i < children.length; i += 1) {
        const child = children[i];
        if (child.tagName === "INPUT" || child.tagName === "TEXTAREA") {
          inputValues[child.name] = child.value;
        }
      }
      return inputValues;
    };

    const newT = {
      id: uuidv4(),
      ...getFormData(e),
      dateTimeAdded: new Date(),
    };
    console.log(newT);
    if (addWhat === "training") {
      newT.topics = [];
      setTKeeper({ newTraining: newT });
    } else if (addWhat === "topic") {
      setTKeeper({ newTopic: newT });
    }

    setAddWindow();
    setTKeeper({});
  };

  const openAddWindow = (addWhat) => {
    setAddWindow(addWhat);
  };

  // const openOption = (e, id, openWhat) => {
  //   const tK = {...tKeeper}

  // }

  const trainingHandlers = {
    select: (e, id) => {
      let tK = { ...tKeeper };
      tK.trainings.forEach((t, i) => {
        tK.trainings[i].isSelected = t.id === id ? true : false;
      });

      setTKeeper({ newTKeeper: tK });
    },
    openAddWindow: openAddWindow,
    add: add,
    deleteItem: (e, id) => {
      setTKeeper({ delTrainingId: id });
    },
  };

  const topicHandlers = {
    add: add,
    openAddWindow: openAddWindow,
    select: (e, id) => {
      const tK = { ...tKeeper };
      const trainings = tK.trainings;
      trainings.forEach((training) => {
        if (training.isSelected) {
          training.topics.forEach((topic) => {
            if (topic.id === id) {
              topic.isSelected = true;
            } else {
              topic.isSelected = false;
            }
          });
        }
      });
      setTKeeper({ newTKeeper: tK });
      function removeSelection() {
        trainings.forEach((training) => {
          if (training.isSelected) {
            training.topics.forEach((topic) => {
              delete topic.isSelected;
            });
          }
        });
        setTKeeper({ newTKeeper: tK });
        this.removeEventListener("mouseleave", removeSelection);
      }
      e.target.parentElement.addEventListener("mouseleave", removeSelection);
    },
    deleteItem: (e, id) => {
      setTKeeper({ delTopicId: id });
    },
    completeItem: (e, id) => {
      const tK = { ...tKeeper };

      tK.trainings.forEach((training, trainingIndex) => {
        training.topics.forEach((topic, topicIndex) => {
          if (topic.id === id) {
            tK.trainings[trainingIndex].topics[topicIndex].isCompleted =
              !topic.isCompleted;
          }
        });
      });

      setTKeeper({ newTKeeper: tK });
    },
  };

  return (
    <>
      <div className="app-training-keeper">
        <button onClick={postData} className="save-btn">
          Save
        </button>
        <TrainingList
          trainings={tKeeper.trainings}
          handlers={trainingHandlers}
        />
        <TopicList topics={tKeeper.topics} handlers={topicHandlers} />
      </div>
      {addWindow.isHidden === false && (
        <Add
          addWindow={addWindow}
          add={trainingHandlers.add}
          removeWindow={setAddWindow}
        />
      )}
    </>
  );
}
