export default function Add({ addWindow, add , removeWindow}) {
  return (
    <>
      <form onSubmit={(e) => add(e, addWindow.addWhat)} className="add-training">
        <h2>Add {addWindow.addWhat} </h2>
        <label aria-label="name">Name:</label>
        <input name="name" type="text"></input>
        <label aria-label="info">Info:</label>
        <textarea name="info" type="text"></textarea>
        <button>Add</button>
      </form>
      <div onClick={() =>removeWindow()} className="backdrop"></div>
    </>
  );
}
