import Topic from "./Topic";

export default function TopicList({ topics, handlers }) {
  const { openAddWindow } = handlers;
  return (
    <div className="topic-list">
      <div className="list-header">
        <h2>Topic List</h2>
        <button onClick={() => openAddWindow("topic")}>+</button>
      </div>
      <ul>
        {topics &&
          topics.map((topic, index) => (
            <Topic key={index} topic={topic} handlers={handlers} />
          ))}
      </ul>
    </div>
  );
}
