export default function Training({ training, handlers }) {
  const { select, openOption, deleteItem } = handlers;
  const isSelected = training.isSelected;
  const options = () => {
    const optButClasses = [
      "training-list-option",
      !training.isSelected && "d-none",
    ];
    return (
      <button
        onClick={(e) => deleteItem(e, training.id)}
        className={optButClasses.join(" ")}
      >
        🚮
      </button>
    );
  };
  return (
    <li
      className={`training-list-item ${isSelected && "black"}`}
      onClick={(e) => select(e, training.id)}
    >
      <span className="list-name">{training.name}</span>
      <span
        className={`${training.isCompleted && "green"} ${
          training.isSelected && "d-none"
        }`}
      >
        ✔
      </span>
      {options()}
    </li>
  );
}
